.Table{background:rgba(0,0,0,0);border:1px solid rgba(73,80,87,.42);padding:10px;text-align:center;border-spacing:0;width:50%;border-radius:10px}.Table thead{font-weight:bolder}.Table thead th,.Table thead td{padding:10px}.Table tbody tr{transition:.2s}.Table tbody tr:hover{background:hsla(210,8.75%,34.3725490196%,.12)}.Table tbody td{padding:10px;border-top:1px solid rgba(73,80,87,.42)}